(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('WithdrawHistoryController', WithdrawHistoryController);

  /** @ngInject */
  function WithdrawHistoryController(BankApi, $log, $rootScope) {
    var vm = this, pageIndex, itemsPerPage, id;

    vm.loadMore = load;
    vm.doRefresh = init;

    init();

    function init() {
      id = 0; // for item list track
      pageIndex = 0;
      itemsPerPage = 20;
      vm.hasMoreData = false;

      vm.items = [];
      load();
    }

    function load() {
      BankApi.withdrawHistory({
        pageIndex: pageIndex,
        itemsPerPage: itemsPerPage
      }).success(function(res) {
        if(res.flag === 1) {
          res.data.reslut.content.forEach(function(_item) {
            _item.id = id;
            vm.items.push(_item);
            id++;
          });
          vm.hasMoreData = res.data.reslut.content.length === itemsPerPage;
        }
      }).finally(function() {
        $rootScope.$broadcast('scroll.refreshComplete');
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      pageIndex++;
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('SpecInvestDetailController', SpecInvestDetailController);

  /** @ngInject */
  function SpecInvestDetailController(BasicApi, $scope, $stateParams, toastr, localStorageService, BridgeService, BILog, $window, OpenAppService) {
    var vm = this;

    vm.productName = $stateParams.name;
    vm.isShare = $stateParams.isShare === 'true';
    vm.intro = [{
      title: ' ',
      desc: '',
      icon: 'nono-cup'
    }, {
      title: ' ',
      desc: '',
      icon: 'nono-plane'
    }, {
      title: ' ',
      desc: '',
      icon: 'nono-basket'
    }];
    vm.list = [];
    vm.banner = [];
    vm.selectDuration = selectDuration;
    vm.minusNum = minusNum;
    vm.addNum = addNum;
    vm.buyProduct = buyProduct;

    // init specInvestDetail info
    init();

    function init() {
      getDetailInfo()
    }

    function getDetailInfo() {
      BasicApi.getSpecInvestDetailInfo({ 'productName': vm.productName }).success(function(res) {
        if (res.flag === 1) {
          vm.list = res.data;
          var len = vm.list.length;
          var initSelectdNum = len - 1 >= 0 ? len - 1 : 0;
          for(var i=len-1; i>=0;i--){
            var status = vm.list[i].fp_status;
            if(status ==3){
              initSelectdNum = i;
              break;

            }
          }
          selectDuration(initSelectdNum);
        }
      });
    }

    function selectDuration(index) {    
      vm.product = vm.list[index] || {};
      var status = vm.product.fp_status;
      if (status == 0) {
        vm.statusText = '';
      } else if (status == 1) {
        vm.statusText = '';
      } else if (status == 3) {
        vm.statusText = '';
      }

      vm.duration = parseFloat(vm.product.fp_expect);
      var banner = vm.product['productMobileDetailImg'];
      if (angular.isString(banner)) {
        vm.banner.push(banner);
      } else {
        vm.banner = banner;
      }
      var priceMax = Math.min(vm.product.fp_price_max,vm.product.balance);
      var increment = vm.product.fp_price_increment;
      vm.maxNum = parseInt(priceMax/increment);
      vm.buyNum = 1;
    }

    function minusNum() {
      vm.buyNum = vm.buyNum - 1;
    }

    function addNum() {
      vm.buyNum = vm.buyNum + 1;
    }

    function buyProduct() {
      if (!vm.isShare) {
        if (vm.buyNum < 1 || !vm.buyNum) {
          toastr.info('');
          return;
        }

        // add BI Log
        orderLog();
        leaveLog();
        
        BridgeService.send({
          type: 'specInvestBuy',
          data: {
            buy_num: vm.buyNum,
            product: vm.product
          }
        });
      } else {
        OpenAppService.openApp();
      }
    }

    // BI Log
    function orderLog() {
      BILog.click({
        page_name: 'product_info',
        page_module: 'order',
        title: 'order',
        page_info: {
          category: 'special',
          product_id: vm.product.fp_id,
        }
      });
    }

    // BI Log
    function leaveLog() {
      BILog.leave({
        page_name: 'product_info',
        page_info: {
          category: 'special',
          product_id: vm.product.fp_id
        }
      });
    }

    $scope.$watch(function() {
      return vm.buyNum;
    }, function(val) {
      if (~~val <= 1) {
        vm.buyNum = 1;
      }
      if (~~val >= vm.maxNum) {
        vm.buyNum = vm.maxNum;
      }
    });

    $scope.$on('$ionicView.beforeLeave', leaveLog);

  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('RecordsListController', RecordsListController);

  /** @ngInject */
  function RecordsListController(UserApi, $state, $rootScope, localStorageService) {
    var vm = this, pageIndex, itemsPerPage;

    vm.loadMore = load;
    vm.detail = detail;

    init();

    function init() {
      pageIndex = 0;
      itemsPerPage = 10;
      vm.records = [];

      load();
    }

    function load() {
      UserApi.investRecords({
        pageIndex: pageIndex,
        itemsPerPage: itemsPerPage
      }).success(function(res) {
        if(res.flag === 1) {
          res.data && res.data.investRecords.forEach(function(_item) {
            vm.records.push(_item);
          });
        }

        vm.hasMoreData = vm.records.length && vm.records.length !== (res.data && +res.data.investRecordNum);
      }).finally(function() {
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      pageIndex++;
    }

    function detail(index) {
      localStorageService.set('record', vm.records[index]);
      $state.go('records:detail');
    }
    
  }
})();

