(function(){
  'use strict';

  angular
    .module('nonoApp')
    .controller('MsgController', MsgController);

  /** @ngInject */
  function MsgController($scope, $rootScope, SystemApi){
    var vm = this, slider;

    $scope.$watch(function() {
      return vm.sliderIndex;
    }, function(val) {
      slider && slider.slideTo && slider.slideTo(+val);
    });

    $scope.$on("$ionicSlides.sliderInitialized", function(event, data){
      // data.slider is the instance of Swiper
      slider = data.slider;
    });

    $scope.$on("$ionicSlides.slideChangeEnd", function(event, data){
      // note: the indexes are 0-based
      vm.sliderIndex = data.slider.activeIndex;
      $scope.$apply();
    });

    vm.notice = {
      hasMoreData: true,
      loadMore: loadNoticeList,
      pageIndex: 0,
      itemsPerPage: 10,
      list: []
    };

    function loadNoticeList() {
      SystemApi.getNoticeList({
        pageIndex: vm.notice.pageIndex,
        itemsPerPage: vm.notice.itemsPerPage
      }).success(function(res) {
        if(res.flag === 1) {
          res.data.result.forEach(function(_item) {
            _item.arc_des = _item.arc_des.slice(0, 40);
            vm.notice.list.push(_item);
          });
          vm.notice.hasMoreData = res.data.result.length === vm.notice.itemsPerPage;
        }
      }).finally(function() {
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      vm.notice.pageIndex++;
    }

    loadNoticeList();

    vm.sys = {
      hasMoreData: true,
      loadMore: loadSysMsgList,
      pageIndex: 0,
      itemsPerPage: 10,
      list: []
    };

    function loadSysMsgList() {
      SystemApi.getSysMsgList({
        pageIndex: vm.sys.pageIndex,
        itemsPerPage: vm.sys.itemsPerPage
      }).success(function(res) {
        if(res.flag === 1) {
          res.data.result.forEach(function(_item) {
            vm.sys.list.push(_item);
          });
          vm.sys.hasMoreData = res.data.result.length === vm.sys.itemsPerPage;
        }
      }).finally(function() {
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      vm.sys.pageIndex++;
    }

    loadSysMsgList();

    
  }

    
})();
(function(){
	'use strict';

	angular
		.module('nonoApp')
		.controller('MsgDetailController', MsgDetailController);

	/** @ngInject */
	function MsgDetailController($stateParams, utils, SystemApi, toastr){
		var vm = this, id = +$stateParams.id, type = +$stateParams.type;
		switch(type) {
			case 0:
				vm.viewTitle = '';
				break;
			case 1: 
				vm.viewTitle = '';
				break;
			case 2:
				vm.viewTitle = '';
				break;
		}

		if(type == 1) {
			SystemApi.getSysMsgDetail({id: id}).success(function(res) {
				if(res.flag != 1) {
					toastr.info(res.msg);
					return;
				}
				var data = res.data;
				if(data){
					vm.title = data.mess_title;
					vm.content = linkFilter(data.mess_content);
					vm.time = data.mess_time ;
				}
			});
			
		} else {
			SystemApi.getNoticeDetail({id: id}).success(function(res) {
				if(res.flag != 1) {
					toastr.info(res.msg);
					return;
				}

				var data = res.data && res.data.result;
				if(data){
					vm.title = data.arc_title;
					vm.content = linkFilter(data.arc_content);
					vm.time = data.create_time;
				}
			});
		}

		function linkFilter(str) {
			// return str.replace(/<a[^<>]*>[^<>]+<\/a>/ig, '');
			return str && str.replace(/href/ig, 'ext-href');
		}
	}

		
})();
