(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('InvestListController', InvestListController);

  /** @ngInject */
  function InvestListController($scope, $state, $stateParams, BasicApi, $rootScope) {
    var vm = this, type = $stateParams.type, productType, title, pageIndex, itemsPerPage;
    vm.sort = 1;

    switch(type) {
      case 'newbie':
        title = '';
        productType = 1;
        break;
      case 'short':
        title = '(12)';
        productType = 2;
        break;
      case 'long':
        title = '(12)';
        productType = 3;
        break;
      case 'rapid1':
        title="-3";
        productType = 4;
        break;
      case 'rapid2':
        title="-3~6";
        productType = 5;
        break;
      case 'rapid3':
        title="-6~12";
        productType = 6;
        break;
      case 'rapid4':
        title="-12";
        productType = 7;
        break;

    }

    vm.title = title;
    vm.productType = productType;
    vm.sortType = '';

    vm.loadMore = load;
    vm.selectTab = selectTab;
    vm.detail = detail;

    init();

    function init() {
      pageIndex = 1;
      itemsPerPage = 6;

      vm.products = [];
      load();
    }

    function load() {
      BasicApi.getPlanList({
        type: productType,
        sortType: vm.sortType,
        sort: vm.sort+1,
        pageIndex: pageIndex,
        itemsPerPage: itemsPerPage
      }).success(function(res) {
        if(res.flag === 1) {
          var list = res.data;

          list && list.forEach(function(item) {
            vm.products.push(item);
          });

          vm.hasMoreData = (list && list.length) === itemsPerPage;
        }
      }).finally(function () {
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      pageIndex++;
    }

    function selectTab(index) {
      switch(index) {
        case 1:
          vm.sortType = 'expect';
          break;
        case 2:
          vm.sortType = 'rate_max';
          break;
        default:
          vm.sortType = '';
      }

      vm.sort = +!vm.sort;

      init();
    }

    function detail(index) {
      var p = vm.products[index];

      $state.go('invest:detail', {type: type, id: p.fp_id});
    }
   
  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('InvestIntroController', InvestIntroController);

  /** @ngInject */
  function InvestIntroController($scope, $stateParams, $rootScope, $ionicSlideBoxDelegate, $log, localStorageService, BasicApi) {
    var vm = this, slider, pageIndex, itemsPerPage;

    // sliders options
    vm.options = {
      loop: false,
      effect: 'fade',
      speed: 500
    };

    vm.sliderIndex = 1;
    vm.loadMore = load;

    $scope.$watch(function() {
      return vm.sliderIndex;
    }, function(val) {
      slider && slider.slideTo && slider.slideTo(+val);
    });

    $scope.$on("$ionicSlides.sliderInitialized", function(event, data){
      // data.slider is the instance of Swiper
      slider = data.slider;
    });

    $scope.$on("$ionicSlides.slideChangeEnd", function(event, data){
      // note: the indexes are 0-based
      vm.sliderIndex = data.slider.activeIndex;
      $scope.$apply();
    });

    init();

    function init() {
      vm.info = localStorageService.get('prodcut.detail');

      initPlanType();

      pageIndex = 1;
      itemsPerPage = 8;

      vm.investedUsers = [];

      load();
    }

    function load() {
      BasicApi.getInvestedUserList({
        id: $stateParams.id,
        pageIndex: pageIndex,
        itemsPerPage: itemsPerPage
      }).success(function(res) {
        if(res.flag === 1) {
           res.data && res.data.forEach(function(_item) {
            var str = _item.user_name;
            if(str && str.length >= 5) {
              _item.username = str.slice(0, 3) + '***' + str.slice(-2);
            } else {
              _item.username = str;
            }

            vm.investedUsers.push(_item);
          });

          vm.hasMoreData = (res.data && res.data.length) === itemsPerPage;
        }
      }).finally(function () {
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });;

      pageIndex++;
    }

    function initPlanType() {
      var img, tosType, insName;
      switch(+vm.info.fp_scope) {
        case 11: // 
          img = 'txjh_pic.png';
          tosType = 4;
          insName = '';
          break;
        case 12: // 
          img = 'lrjh_pic.png';
          tosType = 3;
          insName = '';
          break;
        case 13: // 0
          img = '0yjh_pic.png';
          tosType = 1;
          insName = '0';
          break;
        case 0: // 1
        case 2: // 3
          img = 'jxjh_pic0.png';
          tosType = 2;
          insName = '';
          break;
        case 1: // 
          img = +vm.info.collection_mode ? 'jxjh_pic1.png' : 'jxjh_pic0.png';
          tosType = 5;
          insName = '';
          break;
      }

      vm.descImg = 'assets/images/tos/' + img;
      vm.tosType = tosType;
      vm.insName = insName + '';
      vm.proName = vm.info.fp_title + '';
    }


  }
})();

