(function() {
  'use strict';

  angular
    .module('nonoApp')
    .directive('donut', donut);

  /** @ngInject */
  function donut($log) {
    var directive = {
      restrict: 'E',
      scope: {
        info: '=data'
      },
      replace: true,
      controller: function() {
        function DonutChart(op) {
          this.svgns = "http://www.w3.org/2000/svg";
          this.chart = document.createElementNS(this.svgns, "svg:svg");
          this.size = op.size || 160;
          this.radius = this.size / 2;

          this.chart.setAttribute("width", this.size);
          this.chart.setAttribute("height", this.size);
          this.chart.setAttribute("viewBox", "0 0 " + this.size + " " + this.size);

          // step 1: background circle
          this.drawBackgroundCircle();
          // step 2: sectors
          var offset = 0, self = this;
          op.items.forEach(function(_item) {
            self.drawSector(offset, _item.percentage, _item.color);
            offset += _item.percentage;
          });

          // step 3: forground circle
          this.drawForegroundCircle();
          // step 4: content
          this.drawContent({
            label: op.label,
            color: '#4a4a4a',
            position: 'first'
          });

          this.drawContent({
            label: op.total,
            color: '#2289D9',
            position: 'second'
          });

          return this.chart;
        };

        // Background circle
        DonutChart.prototype.drawBackgroundCircle = function() {
          var back = document.createElementNS(this.svgns, "circle");
          back.setAttributeNS(null, "cx", this.radius);
          back.setAttributeNS(null, "cy", this.radius);
          back.setAttributeNS(null, "r", this.radius);
          var color = "#d0d0d0";
          if (this.size > 50) {
            color = "#ebebeb";
          }
          back.setAttributeNS(null, "fill", color);

          this.chart.appendChild(back);
        };

        // Foreground circle
        DonutChart.prototype.drawForegroundCircle = function() {
          var front = document.createElementNS(this.svgns, "circle");
          front.setAttributeNS(null, "cx", (this.radius));
          front.setAttributeNS(null, "cy", (this.radius));
          front.setAttributeNS(null, "r", (this.radius * 0.6)); //about 60% as big as back circle 
          front.setAttributeNS(null, "fill", "#fff");

          this.chart.appendChild(front);
        };

        DonutChart.prototype.drawSector = function(offsetpercent, percentage, color) {
          // primary wedge
          var path = document.createElementNS(this.svgns, "path");
          var unit = (Math.PI * 2) / 100;
          var radius = this.radius;
          var startangle = offsetpercent * unit;
          var endangle = startangle + percentage * unit - 0.001;
          var x1 = (radius) + (radius) * Math.sin(startangle);
          var y1 = (radius) - (radius) * Math.cos(startangle);
          var x2 = (radius) + (radius) * Math.sin(endangle);
          var y2 = (radius) - (radius) * Math.cos(endangle);
          var big = 0;
          if (endangle - startangle > Math.PI) {
            big = 1;
          }
          var d = "M " + (radius) + "," + (radius) + // Start at circle center
            " L " + x1 + "," + y1 + // Draw line to (x1,y1)
            " A " + (radius) + "," + (radius) + // Draw an arc of radius r
            " 0 " + big + " 1 " + // Arc details...
            x2 + "," + y2 + // Arc goes to to (x2,y2)
            " Z"; // Close path back to (cx,cy)
          path.setAttribute("d", d); // Set this path 
          path.setAttribute("fill", color || '#f05f3b');

          this.chart.appendChild(path); // Add wedge to chart
        };

        // Content
        DonutChart.prototype.drawContent = function(_op) {
          _op = _op || {};
          var text = document.createElementNS(this.svgns, "text");
          text.setAttributeNS(null, 'x', '50%');
          // vertical position
          var y = this.radius,
            fs = 14;
          if (_op.position === 'first') {
            y -= 5;
            fs = 12;
          } else if (_op.position === 'second') {
            y += 15;
          }
          text.setAttributeNS(null, 'y', y);
          text.setAttributeNS(null, 'font-size', fs);
          text.setAttributeNS(null, 'text-anchor', 'middle');
          text.setAttributeNS(null, "fill", _op.color);
          text.innerHTML = _op.label;

          this.chart.appendChild(text);
        }

        this.getChart = function(data) {
          return new DonutChart(data);
        };

      },
      link: function(scope, element, attr, ctrl) {
        scope.$watch('info', function(val) {
          if(val) {
            var chart = ctrl.getChart(val);
            element.empty();
            element.append(chart);
          }
        }, true);
      }
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .directive('coupon', coupon);

  /** @ngInject */
  function coupon($state, $log, $timeout, $filter) {
    var directive = {
      restrict: 'E',
      scope: {
        data: '=',
        type: '@'
      },
      replace: true,
      templateUrl: 'app/components/coupon/coupon.html',
      link: function(scope, element, attrs) {
        var d = scope.data;

        switch(scope.type) {
          case 'coupon':
          case 'interest':
            scope.info = {
              name: d.uv_approach,
              value: d.value,
              isUsed: +d.is_used,
              isExpired: +d.statue === 4,
              remark: d.uv_remark,
              expireDate: d.stop_time,
              type: scope.type === 'coupon' ? '<br /><br />' : '<br /><br />'
            };
            break;
          case 'pp': 
            scope.info = {
              name: d.type,
              value: d.ucel_cash,
              isUsed: false,
              isExpired: +d.ucel_status === 10,
              remark: '',
              expireDate: $filter('date')(new Date(d.ucel_end_time), 'yyyy-MM-dd'),
              type: '<br /><br /><br />'
            };
            break;
          case 'rp':
            scope.info = {
              name: '',
              value: d.gain_amount,
              isUsed: +d.gain_status === 2,
              isExpired: +d.gain_status === 3,
              remark: d.package_resource,
              expireDate: d.end_date,
              type: '<br /><br /><br />'
            };
            break;
        }
      }
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .directive('compile', compile);

  /** @ngInject */
  function compile($compile) {
    var directive = {
      restrict: 'A',
      link: function(scope, element, attrs) {
        scope.$watch(
          function(scope) {
            // watch the 'compile' expression for changes
            return scope.$eval(attrs.compile);
          },
          function(value) {
            // when the 'compile' expression changes
            // assign it into the current DOM
            element.html(value);

            // compile the new DOM and link it to the current
            // scope.
            // NOTE: we only compile .childNodes so that
            // we don't get into infinite loop compiling ourselves
            $compile(element.contents())(scope);
          }
        );
      }
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .directive('cSliders', cSliders);

  /** @ngInject */
  function cSliders($state, $log, $timeout) {
    var directive = {
      restrict: 'E',
      scope: {
        sliders: '='
      },
      templateUrl: 'app/components/c-sliders/c.sliders.html',
      link: function(scope, element, attrs) {
        var sx, // start x
          ex, // end x
          tx, // translate x
          animating = false,
          movement,
          $sliders,
          $slider,
          len,
          containerWidth,
          sliderWidth;

        scope.$watch('sliders', function(val) {
          if(val) {
            init();
          }
        });

        scope.select = function(index) {
          var item = scope.sliders[index];
          $state.go('external', {name: item.name, link: item.link});
        };

        function init() {
          $sliders = document.querySelector('.c-sliders');
          $slider = document.querySelectorAll('.c-slider');
          len = $slider.length;
          containerWidth = document.querySelector('.c-sliders-container').clientWidth;
          sliderWidth = document.querySelector('.c-sliders-container .c-slider').clientWidth;

          for (var i = 0; i < len; i++) {
            $slider[i].style.left = i * sliderWidth + 'px';
          }

          tx = (containerWidth - sliderWidth) / 2 - sliderWidth;
          transform(tx);
        }

        function move(direction) {
          // $log.debug('direction', direction);

          var firstEle = document.querySelector('.c-slider');
          var lastEle = document.querySelector('.c-slider:last-child');

          if(direction === 'ftl') { // first to last
            firstEle.style.left = +firstEle.style.left.replace('px', '') + sliderWidth * len + 'px';
            $sliders.appendChild(firstEle, lastEle);
          } else if(direction === 'ltf') { // last to first
            lastEle.style.left = +lastEle.style.left.replace('px', '') - sliderWidth * len + 'px';
            $sliders.insertBefore(lastEle, firstEle);
          }
        }

        element.on('touchstart', function(e) {
          sx = e.touches[0].pageX;
          animating = true;
          tx = getTx();
          movement = '';
        });

        element.on('touchmove', function(e) {
          e.preventDefault();
          e.stopPropagation();

          if (!animating) return;

          ex = e.touches[0].pageX;

          transform(tx + ex - sx);

          var critical = sliderWidth - (containerWidth - sliderWidth) / 2;
          var cur = ex - sx; // current
          if(cur < 0) { // slide left
            if(cur + critical > 0 && cur + critical < 10 && movement === 1) {
              move('ltf');
              movement = 0;
            } else if(cur + critical < 0 && cur + critical > -10 && !movement) {
              move('ftl');
              movement = 1;
            } else if(cur + critical + sliderWidth > 0 && cur + critical + sliderWidth < 10 && movement === 2) {
              move('ltf');
              movement = 1;
            } else if(cur + critical + sliderWidth < 0 && cur + critical + sliderWidth > -10 && movement === 1) {
              move('ftl');
              movement = 2;
            }
          } else { // slide right
            if(cur - critical < 0 && cur - critical > -10 && movement === 1) {
              move('ftl');
              movement = 0;
            } else if(cur - critical > 0 && cur - critical < 10 && !movement) {
              move('ltf');
              movement = 1;
            } else if(cur - critical - sliderWidth < 0 && cur - critical - sliderWidth > -10 && movement === 2) {
              move('ftl');
              movement = 1;
            } else if(cur - critical - sliderWidth > 0 && cur - critical - sliderWidth < 10 && movement === 1) {
              move('ltf');
              movement = 2;
            }
          }
        });

        element.on('touchend', function(e) {
          animating = false;
          tx = getTx();
          // center element
          tx = Math.floor(tx / sliderWidth) * sliderWidth + (containerWidth - sliderWidth) / 2;

          transform(tx);

          var checker = function() {
            var doCheck = function() {
              var firstEle = document.querySelector('.c-slider');
              var lastEle = document.querySelector('.c-slider:last-child');
              if(firstEle.getBoundingClientRect().left > 0) {
                move('ltf');
                checker();
              } else if(lastEle.getBoundingClientRect().right < containerWidth) {
                move('ftl');
                checker();
              }
            };

            if(requestAnimationFrame) {
              requestAnimationFrame(doCheck);
            } else { // Android 4.3 and below
              $timeout(doCheck, 1000/60);
            }
          };

          checker();
        });

        function getTx() {
          var style = getComputedStyle($sliders);
          var matrix = style.getPropertyValue("-webkit-transform") ||
                       style.getPropertyValue("-moz-transform") ||
                       style.getPropertyValue("-ms-transform") ||
                       style.getPropertyValue("-o-transform") ||
                       style.getPropertyValue("transform");

          return +matrix.split(',')[4] || 0;
        }

        function transform(x) {
          $sliders.style.transform = 'translate3d(' + x + 'px,0,0)';
        }
      }
    };

    return directive;
  }

})();

