(function() {
  'use strict';

  angular
    .module('nonoApp')
    .factory('utils', utils);

  /** @ngInject */
  function utils($ionicHistory, $timeout, $ionicPopup, $log, $rootScope, md5, $filter) {
    return {
      getLocationSearch: function() {
        var query_string = {};
          var query = window.location.search.substring(1);
          var vars = query.split("&");
          for (var i=0;i<vars.length;i++) {
            var pair = vars[i].split("=");
                // If first entry with this name
            if (typeof query_string[pair[0]] === "undefined") {
              query_string[pair[0]] = decodeURIComponent(pair[1]);
                // If second entry with this name
            } else if (typeof query_string[pair[0]] === "string") {
              var arr = [ query_string[pair[0]],decodeURIComponent(pair[1]) ];
              query_string[pair[0]] = arr;
                // If third or later entry with this name
            } else {
              query_string[pair[0]].push(decodeURIComponent(pair[1]));
            }
          }

          return query_string;
      },
      getAjaxHeaders: function() {
        return function() {
          var now = new Date(),
              s1 = $filter('date')(now, 'yyyy-MM-dd HH'),
              s2 = Math.floor(now.getMinutes()/5),
              v = 'm.nonobank.com/msapi/' + s1 + s2;

          return {
            'Authorization': md5.createHash(v),
            'ComeFrom': 'MaiZI'
          };
        }
      },
      disableBack: function() {
        $ionicHistory.nextViewOptions({
          disableAnimate: false,
          disableBack: true
        });
      },
      goBack: function(depth) {
        $ionicHistory.goBack(depth);
      },
      resendCountdown: function($scope) {
        $scope.resendCountdown = 0;
        
        return function() {
          $scope.resendCountdown = 60;
          var countdown = function() {
            if($scope.resendCountdown > 0) {
              $scope.resendCountdown += -1;
              $timeout(countdown, 1000);
            }
          };
          countdown();
        };
      },
      dhms: function(t) {
        var days, hours, minutes, seconds;
        days = Math.floor(t / 86400);
        t -= days * 86400;
        hours = Math.floor(t / 3600) % 24;
        t -= hours * 3600;
        minutes = Math.floor(t / 60) % 60;
        t -= minutes * 60;
        seconds = t % 60;
        if(days){
           return [
            days + '',
            hours + '',
            minutes + '',
            seconds + ''
          ].join('');

        }else{
          return [
            hours + '',
            minutes + '',
            seconds + ''
          ].join('');
        }
      },
      formatPhone: function(num) {
        var reg = /(\d{3})\d{4}(\d{4})/;
        var _num = num.replace(reg,'$1****$2');
        return _num;
      },
      alert: function(obj) {
        var alertPopup = $ionicPopup.alert({
          title: obj.title || '',
          cssClass: obj.cssClass || 'text-center',
          subTitle: obj.subTitle || '',
          template: obj.content || '',
          templateUrl: obj.contentUrl || '',
          okText: obj.okText || '',
          okType: obj.okType || 'button-positive'
        });
        alertPopup.then(function() {
          obj.onOk && obj.onOk();
        });
      },
      confirm: function(obj) {
        var confirmPopup = $ionicPopup.confirm({
          title: obj.title || '',
          template: obj.content || '',
          cssClass: obj.cssClass || 'text-center',
          okText: obj.okText || '',
          okType: obj.okType || 'button-positive',
          cancelText: obj.cancelText || ''
        });
        confirmPopup.then(function(res) {
          if(res) {
            obj.onOk && obj.onOk();
          } else {
            obj.onCancel && obj.onCancel();
          }
        });
      }
    }
  }

})();
(function() {
  'use strict';

  angular
    .module('nonoApp')
    .service('UserService', UserService);

  /** @ngInject */
  function UserService(UserApi, $log, $location, $ionicHistory, user, localStorageService, $timeout, $state) {
    var self = this;

    self.init = function() {
      var search = $location.search();
      if(search.sessionId) { // from native
        user.sessionId = search.sessionId;
        removeUser();
      } else {
        login();
      }
    };

    self.setUser = function(_user) {
      user.sessionId = _user.session_id;
      user.mId = _user.m_id;
      user.realname = _user.realname;
      user.idNo = _user.idnum;
      user.phone = _user.mobilenum;
      user.level = _user.level;

      user.hasPayPwd = +_user.is_pay_password;
      user.hasCard = +_user.is_bank_card;

      // for login
      user.username = _user.username;
      user.password = _user.password;

      localStorageService.set('user', user);

      // trigger timer
      $timeout(login, 14*60*1000); // 14min
    };

    self.getUser = function() {
      return localStorageService.get('user');
    };

    self.setAccountInfo = function(info) {
      localStorageService.set('account', info);
    };

    self.getAccountInfo = function() {
      return localStorageService.get('account');
    };

    self.updateAccountInfo = function(info) {
      var _info = self.getAccountInfo() || {};
      angular.merge(_info, info);
      self.setAccountInfo(_info);
    };

    self.logout = function() {
      removeUser();
      user.sessionId = null;
      $state.go('home');

      $timeout(function(){
        $ionicHistory.clearCache();
        $ionicHistory.clearHistory();
      }, 1000);
    };

    function removeUser() {
      localStorageService.remove('user', 'account');
    };

    // user for first time
    function login() {
      var _user = self.getUser() || {};

      angular.merge(user, _user);

      if(_user.username && _user.password) {
        UserApi.login(_user).success(function(res) {
          if(res.flag === 1) {
            angular.merge(_user, res.data);
            self.setUser(_user);
          } else {
            removeUser();
          }
        });
      }
    }

  }
})();

