(function() {
  'use strict';

  angular
    .module('nonoApp')
    .service('BridgeService', BridgeService);

  /** @ngInject */
  function BridgeService($log, $rootScope, $ionicScrollDelegate, CLIENT_VERSION) {
    var self = this, 
      isAndroid = ionic.Platform.isAndroid(),
      newIosBridge = CLIENT_VERSION >= '4.4.0' && !isAndroid;

    function setupNewIosBridge(callback) {
      if (window.WebViewJavascriptBridge) { return callback(WebViewJavascriptBridge); }
      if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(callback); }
      window.WVJBCallbacks = [callback];
      var WVJBIframe = document.createElement('iframe');
      WVJBIframe.style.display = 'none';
      WVJBIframe.src = 'wvjbscheme://__BRIDGE_LOADED__';
      document.documentElement.appendChild(WVJBIframe);
      setTimeout(function() { document.documentElement.removeChild(WVJBIframe) }, 0);
    }

    self.init = function() {
      $log.info('init BridgeService');

      if(window.WebViewJavascriptBridge) {
        self.bridge = window.WebViewJavascriptBridge;
      } else {
        document.addEventListener('WebViewJavascriptBridgeReady', function() {
          self.bridge = WebViewJavascriptBridge;
        }, false);
      }

      if(newIosBridge) {
        setupNewIosBridge(function(bridge) {
          self.bridge = bridge;

          // reset scroll
          bridge.registerHandler('pageShow', function(data, responseCallback) {
            $ionicScrollDelegate.scrollTop(true);
            responseCallback && responseCallback();
          });
        });
      }

      // for handle android back event
      window.back = function() {
        $rootScope.$ionicGoBack();
      };
    };

    self.backToApp = function() {
      self.send({type: 'backToApp'});
    };

    self.send = function(obj) {
      $log.info(obj);
      
      if(self.bridge) {
        if(isAndroid) {
          obj = JSON.stringify(obj);
        } 

        if(newIosBridge) {
          self.bridge.callHandler('nonobankIos', obj, function(data) {
            obj.callback && obj.callback(data);
          });
        } else {
          self.bridge.send(obj);
        }
      } else {
        $log.error('no js bridge');
      }
    } 

  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .service('BILog', BILog);

  /** @ngInject */
  function BILog(BridgeService, $log) {
    var enterTime, lastEnterTime;

    this.enter = function() {
      // `$stateChangeSucess` would be called before `$ionicView.beforeLeave`
      lastEnterTime = enterTime || Date.now();
      enterTime = Date.now();
    };

    // page_name, page_info
    this.leave = function(obj) {
      var base = getBasicInfo();

      log(base, obj);
    };

    // page_name, page_module, title, page_info
    this.click = function(obj) {
      var base = getBasicInfo(true);

      log(base, obj);
    };

    function log(base, info) {
      angular.merge(base, info);
      $log.debug('BILog', base);

      BridgeService.send({
        type: 'BILog',
        data: base
      });
    };

    function getBasicInfo(isClick) {
      var now = Date.now();

      var obj = {
        // 'log_id': '',
        'log_time': now,
        'start_time': isClick ? now : lastEnterTime,
        'end_time': now,
        'action_type': isClick ? 'click_event' : 'view_event'
      };

      return obj;
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .service('BankService', BankService);

  /** @ngInject */
  function BankService(UserApi, $log, user, localStorageService, BankApi) {
    var self = this;

    self.bankList = [];
    self.cardList = []; // bound cards
    self.selected = {};

    self.select = select;

    init();

    function descFormatter(str) {
      return str && str.replace(/,/g, '').replace(/:/g, '').replace('', '<br>');
    }

    function init() {
      self.bankList.length = 0;
      BankApi.getBankList().success(function(res) {
        if(res.flag === 1) {
          res.data.forEach(function(_item) {
            var item = {
              id: _item.id,
              name: _item.name,
              limitAmount: _item.limit_value,
              limitDesc: descFormatter(_item.limit),
              channel: _item.fast_payment_channel,
              cardType: _item.band_card_type,
              typeFlag: _item.typeFlag
            };

            self.bankList.push(item);
          });
        }
      });

      self.cardList.length = 0;
      BankApi.getCardList().success(function(res) {
        if(res.flag === 1) {
          res.data.forEach(function(_item, _index) {
            var item = {
              id: _item.banks_id,
              name: _item.banks_cat,
              account: _item.banks_account,
              branch: _item.banks_name,
              tail: _item.banks_account.substr(-4),
              authType: _item.auth_type, //1:2:3:
              limitDesc: descFormatter(_item.limit),
              limitAmount: _item.limit_value
            };

            if(_index === 0) {
              item.selected = true;
              angular.merge(self.selected, item);
            }

            self.cardList.push(item);
          });
        }
      })
    }

    function select(index) {
      self.cardList.forEach(function(_item) {
        _item.selected = false;
      });

      self.cardList[index].selected = true;
      angular.merge(self.selected, self.cardList[index]);
    }

    

  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .service('AppService', AppService);

  /** @ngInject */

  function AppService($ionicModal, $ionicPopup, $rootScope, $timeout, $state, UserApi, toastr, APISERVER, UserService, utils) {
    var self = this,
      modal, options, mScope = $rootScope.$new(true),
      noteScope = $rootScope.$new(true),
      notePopup, noteOption;

    mScope.user = {}; // modal model
    mScope.countdown = 0;

    mScope.fn = {}; // fix method can't pass to template
    mScope.fn.close = closeModal;
    mScope.fn.refreshImgCode = refreshImgCode;
    mScope.fn.doLogin = doLogin;
    mScope.fn.doRegister = doRegister;
    mScope.fn.doRetrievePwd = doRetrievePwd;
    mScope.fn.sendRegSmsCode = sendRegSmsCode;

    // page switch
    mScope.fn.goRegister = goRegister;
    mScope.fn.goLogin = goLogin;

    // export
    self.login = function(_options) {
      initModal('app/components/login/login.modal.html', _options);
    };

    // export
    self.register = function(_options) {
      initModal('app/components/login/register.modal.html', _options);

      refreshImgCode();
    }

    function initModal(templateUrl, _options) {
      $ionicModal.fromTemplateUrl(templateUrl, {
        scope: mScope,
        animation: 'slide-in-up'
      }).then(function(_modal) {
        modal = _modal;

        modal.show();
      });

      options = _options;
    }

    function closeModal() {
      mScope.user = {}; // reset model
      modal.remove();

      // call on error
      options && options.onCancel && options.onCancel();
    }

    function refreshImgCode() {
      mScope.imgCode = UserApi.getSessionId().success(function(res) {
        if(res.flag === 1) {
          var sessionId = res.data['session_id'];
          mScope.user.sessionId = sessionId
          mScope.imgCode = APISERVER.MSAPI + '/randomImage?sessionId=' + sessionId;
        }
      });
    }

    function sendRegSmsCode() {
      UserApi.sendRegSmsCode(mScope.user).success(function(res) {
        if(res.flag === 1) {
          mScope.user.sessionId = res.data.session_id;
          startCountdown();
        }

        toastr.info(res.msg);
      });
    }

    function doLogin() {
      UserApi.login(mScope.user).success(ajaxHandler);
    }

    function doRegister() {
      if(!/^1\d{10}/.test(mScope.user.username)) {
        toastr.info('');
        return;
      }
      if(!/^(?!\d+$|[a-zA-Z]+$|[\W-_]+$)[\s\S]{6,16}$/.test(mScope.user.password)) {
        toastr.info('6-16');
        return;
      }
      UserApi.register(mScope.user).success(ajaxHandler)
    }

    function doRetrievePwd() {

    }

    function ajaxHandler(res) {
      if(res.flag === 1) {
        res.data.username = mScope.user.username;
        res.data.password = mScope.user.password;
        UserService.setUser(res.data);

        closeModal();

        // call on success
        $timeout(function() {
          options && options.onSuccess && options.onSuccess();
        }, 300);
      } else {
        toastr.info(res.msg);
      }
    }

    function startCountdown() {
      mScope.countdown = 30;
      var fn = function() {
        if(mScope.countdown) {
          mScope.countdown--;

          $timeout(fn, 1000);
        }
      };

      fn();
    }

    // switch to register flow
    function goRegister() {
      closeModal();
      self.register();
    }

    // switch to login flow
    function goLogin() {
      closeModal();
      self.login();
    }

    noteScope.fn = {};
    self.showNotePopup = function(_option) {
      noteScope.data = {};

      UserApi.heguiCheck().success(function(res) {
        noteScope.data = res.data;
        if (res.errorCode == '0100281') {
          notePopup = $ionicPopup.show({
            cssClass: 'login-note-popup',
            templateUrl: 'app/components/login/note.popup.html',
            title: '',
            scope: noteScope
          });
        }
      });
      noteOption = _option;
    }
    noteScope.fn.close = function() {
      notePopup.close();
      noteOption.onClose && noteOption.onClose();
    }

    $rootScope.$on('login:show', function() {
      modal.show();
    });

  }
})();

