(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('EntertainmentController', EntertainmentController);

  /** @ngInject */
  function EntertainmentController($state, BridgeService, user, SystemApi, AppService) {
    var vm = this;
    SystemApi.EntertainmentList().success(function(res) {
      vm.items = res.data;   
    })
   
    vm.select = select;

    function select(index) {
      var selectItem = vm.items[index];

      if(BridgeService.bridge) {
        BridgeService.send({
          type:'activity',
          data:{
            name: selectItem['title'],
            link: selectItem['link'],
            needLogin: selectItem['needLogin'] || true
          }
        });
      } else {
        if(user.sessionId) {
          external(selectItem);
        } else {
          AppService.login({
            onSuccess: function() {
              external(selectItem);
            }
          });
        }
      }
    }

    function external(item) {
      $state.go('external', {
        name: item.title,
        link: item.link + '?sessionId=' + user.sessionId
      });
    }
    
  }
})();

(function() {
  'use strict';

  angular
    .module('nonoApp')
    .controller('EarningsController', EarningsController);

  /** @ngInject */
  function EarningsController(UserService, UserApi, $log, $rootScope) {
    var vm = this, type, pageIndex = 0, itemsPerPage = 15;

    vm.toggle = toggle;
    vm.detail = {};
    vm.loadMore = load;

    init();

    function init() {
      UserApi.getEarningsInfo().success(function(res) {
        if(res.flag === 1) {
          var data = res.data;

          vm.info = {
            label: '',
            total: +data.myearnings,
            items: [
              {
                type: 'financePlan',
                name: '',
                amount: +data.financePlan.earnings,
                percentage: 100 * +data.financePlan.earnings / +data.myearnings,
                color: '#ffce29'
              },
              {
                type: 'lend',
                name: '',
                amount: +data.lend.earnings,
                percentage: 100 * +data.lend.earnings / +data.myearnings,
                color: '#ffa429'
              },
              {
                type: 'intimatePlan',
                name: '',
                amount: +data.intimatePlan.earnings,
                percentage: 100 * +data.intimatePlan.earnings / +data.myearnings,
                color: '#7a70e7'
              },
              {
                type: 'lazyPlan',
                name: '',
                amount: +data.lazyPlan.earnings,
                percentage: 100 * +data.lazyPlan.earnings / +data.myearnings,
                color: '#6ccaef'
              },
              {
                type: 'caishenyeye',
                name: '',
                amount: +data.caishenyeye.earnings,
                percentage: 100* +data.caishenyeye.earnings / +data.myearnings,
                color: '#1eca9e'
              }
            ],
            size: 160
          };
        }
      });
    }

    function toggle(index) {
      vm.showDetail = !vm.showDetail;
      vm.info.items.forEach(function(_item) {
        _item.hide = !_item.hide;
      });
      vm.info.items[index].hide = false;

      type = vm.info.items[index].type;

      if(vm.showDetail) {
        load();
      } else {
        pageIndex = 0;
        vm.detail = {};
      }
    }

    function load() {
      UserApi.getEarningList({
        type: type,
        pageIndex: pageIndex,
        itemsPerPage: itemsPerPage
      }).success(function(res) {
        if(res.flag === 1) {
          angular.merge(vm.detail, res.data);

          var len = 0;
          angular.forEach(res.data, function(value, key) {
            angular.forEach(value, function() {
              len++;
            });
          });

          vm.hasMoreData = len === itemsPerPage;
        } else {
          vm.hasMoreData = false;
        }
      }).finally(function() {
        $rootScope.$broadcast('scroll.infiniteScrollComplete');
      });

      
      pageIndex++;
    }

    

  }
})();

